:root {
  --primary: #0d1e4d;
  --secondary: #f7ede2;

  --tertiary: #828c51;
  --dark: #f28482;
  --light: #f5cac3;
  --darkgry: #54494b;
  --ltgry: #a8bccb;
}

* {
  box-sizing: border-box;
}

main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 400;
  line-height: 1.5;
  font-family: Arial, Helvetica, sans-serif;
  min-height: 100%;
  color: var(--primary);
  background-color: var(--ltgry);
  display: flex;
  flex-wrap: wrap;
}

header {
  padding: 1rem;
  background: var(--primary);
  color: var(--secondary);
  margin: 0;
  display: flex;
  flex-wrap: wrap;
}

header a {
  color: var(--secondary);
  list-style: none;
  text-decoration: none;
}

img {
  margin: 10px;
  padding: 5px;
  border: 2px solid var(--dark);
  border-radius: 14px;
  display: flex;
  flex-wrap: wrap;
}

.container {
  position: relative;
  width: 45%;
}


.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.li {
  list-style: none;
  margin: 1rem;
  text-decoration: none;
}

.section-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
}

.project-grid-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-content: stretch;
  align-items: stretch;
  grid-gap: 5px;
  text-align: center;
}

.project-grid-item {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  max-width: 90%;
  margin: 10px;
  padding: 10px;
  border: 2px solid var(--primary);
  border-radius: 14px;
  justify-content: center;
  align-items: center;
  /* background-color: var(--tertiary); */
}

.project-grid-img {
  max-width: 90%;
}

button {
  margin: 5px;
  border: 2px solid var(--primary);
  border-radius: 5px;
  color: var(--primary);
  background-color: var(--light);
}